import { Component, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Router, NavigationEnd } from '@angular/router';
import { filter } from 'rxjs/operators';

import { MatCardModule } from '@angular/material/card';
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatToolbarModule } from '@angular/material/toolbar';
import { MatListModule } from '@angular/material/list';
import { MatIconModule } from '@angular/material/icon';
import { RouterModule } from '@angular/router';

import { UserService } from '../../services/user';
import { VehicleService } from '../../services/vehicle.service';
import { BookingService } from '../../services/booking.service';

@Component({
  standalone: true,
  selector: 'app-dashboard',
  imports: [
    CommonModule,
    RouterModule,
    MatCardModule,
    MatSidenavModule,
    MatToolbarModule,
    MatListModule,
    MatIconModule
  ],
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  user: any = {};
  vehicles: any[] = [];
  bookings: any[] = [];

  constructor(
    private userService: UserService,
    private vehicleService: VehicleService,
    private bookingService: BookingService,
    private router: Router
  ) {
    this.router.events
      .pipe(filter(event => event instanceof NavigationEnd && this.router.url === '/dashboard'))
      .subscribe(() => {
        this.loadRegisteredVehicles();
        this.loadBookings();
      });
  }

  ngOnInit(): void {
    this.loadUserDetails();
    this.loadRegisteredVehicles();
    this.loadBookings();
  }

  loadUserDetails() {
    this.userService.getCurrentUser().subscribe({
      next: (res) => {
        this.user = res?.data || res;
      },
      error: (err) => {
        console.error('Failed to fetch user:', err);
        this.router.navigate(['/login']);
      }
    });
  }

  loadRegisteredVehicles() {
    this.vehicleService.getUserVehicles().subscribe({
      next: (res) => {
        this.vehicles = res;
      },
      error: (err) => {
        console.error('Failed to load vehicles:', err);
      }
    });
  }

  loadBookings() {
    this.bookingService.getUserBookings().subscribe({
      next: (res) => {
        this.bookings = res;
      },
      error: (err) => {
        console.error('Failed to load bookings:', err);
      }
    });
  }

  logout() {
    this.userService.logout();
    this.router.navigate(['/login']);
  }

  goToVehicleRegister() {
    this.router.navigate(['/vehicle-register']);
  }
}
